<script>
export default {
    name: 'XAuthenticator',
    props: {
        splash: {
            type: Boolean,
            default: true
        },
        splashColor: {
            type: String,
            default: '#fafafa'
        },
        splashTheme: {
            type: String,
            default: 'one'
        },
        avatar: {
            type: String,
            default: null
        },
        title: {
            type: String,
            default: 'Por favor, informe sua credencial.'
        },
        flat: {
            type: Boolean,
            default: false
        },
        color: {
            type: String,
            default: 'primary'
        },
        dark: {
            type: Boolean,
            default: false
        },
        showPassword: {
            type: Boolean,
            default: true
        },
        showRecovery: {
            type: Boolean,
            default: true
        },
        showShortcutsApps: {
            type: Boolean,
            default: true
        },
    },
    computed: {

    },
    data() {
        return {
            step: 0,
            passwordToggle: false,
            newPwdToggle: false,
            confirmPwdToggle: false,

            loading: {
                login: false,
            },

            snackMessage: {
                show: false,
                text: null
            },

            rules: {
                isValid: {
                    frmLogin: false,
                    frmPwdRecovery: false,
                    frmRecoveryCode: false,
                },
                required: (value) => {
                    return !!value || 'Esse campo é obrigatório!';
                },
                email: (value) => {
                    const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                    return pattern.test(value) || 'E-mail inválido!';
                },
                confirmPwd: (value) => {
                    if (value !== this.passwordRecovery.newPwd) {
                        return 'As senha informadas devem ser iguais!';
                    }
                    return true;
                },
            },

            credential: {
                username: 'MASTER',
                password: '123456',
                device: {}
            },

            passwordRecovery: {
                code: null,
                email: null,
                newPwd: null,
                confirmPwd: null
            },
        }
    },
    methods: {

        showMessage(msg, delay) {
            // this.snackMessage.text = msg;
            // this.snackMessage.show = true;
        },

        showPasswordButton(show) {
            return show ? 'mdi-eye-off-outline' : 'mdi-eye-outline';
        },

        resetFields() {
            if (this.step === 4) {
                this.passwordRecovery = {};
                this.credential = {};
            }
            if (this.step === 3) {
                this.passwordRecovery.newPwd = null;
                this.passwordRecovery.confirmPwd = null;
            }
            if (this.step === 2) {
                this.passwordRecovery.code = null;
            }
        },

        setStep(step) {
            switch (step) {
                case 0:
                    this.resetFields();
                    // setTimeout(() => this.$refs.usernameRef.focus());
                    break;
                case 1:
                    // setTimeout(() => this.$refs.emailRef.focus());
                    break;
                case 2:
                    this.resetFields();
                    // setTimeout(() => this.$refs.codeRef.focus());
                    break;
                case 3:
                    this.resetFields();
                    // setTimeout(() => this.$refs.newPwdRef.focus());
                    break;
                default:
                    break;
            };

            this.step = step;
        },

        async login() {
            try {
                this.loading.login = true;

                // const user = await this.$store.dispatch('sgu/login', this.credential);

                // this.$emit('loginSuccess', user);
            } catch (err) {
                // this.showMessage(err.message);
                // this.$emit('loginError', err.message);
                // setTimeout(() => this.$refs.usernameRef.focus());
            } finally {
                this.loading.login = false;
            }
        },

        async logout() {
            // await this.$store.dispatch('sgu/logout');
            // this.$emit('logout');
        },

        sendMailPwdRecover() {
            this.setStep(2);

            // this.$emit('step1', { status: 'SUCCESS', data: this.passwordRecovery });
        },

        checkRecoverCode() {
            this.setStep(3);

            // this.$emit('step2', { status: 'SUCCESS', data: this.passwordRecovery });
        },

        resetPassword() {
            this.setStep(4);

            // this.$emit('step3', { status: 'SUCCESS', data: this.passwordRecovery });
        },

        setCredentialDevice() {
            // this.credential.device = session.App().device;
        },
    },
    created() {
        this.setStep(0);
        this.logout();
    },
